import React from 'react';
import "./Forms.css";
import Spinner from "./Spinner/Spinner";
import axios from 'axios';

export default class Forms extends React.Component {
	state={
		link: "",
		filter: false,
		spinner: false,
		btnText: false,
		alertDaner: true,
		alertSuccess: true,
		msg: "", 
	}
	onLink=(e)=>{
		this.setState({
			link: e.target.value
		})
	}
	onShort=()=>{
		this.setState({
			filter: true,
			spinner: true,
			btnText: true
		})
		const {link} = this.state;
		axios.post("/create", { link })
		    .then(res => {
		        console.log(res.data);
		        if(res.data.err === 1){
		        	this.setState({
			        	alertDaner: false,
						msg: res.data.msg,
						spinner: false,
						filter: true,
						btnText: false,
			        })
		        }else{
		        	 this.setState({
						alertDaner: true,
			        	filter: true,
			        	spinner: false,
			        	btnText: false,
			        	link:res.data.link,
			        })
		        }
		           
		    })
		    .catch(res =>{
		    	alert("Мы не смогли сократить ссылку")
		    	this.setState({
		        	filter: true,
		        	spinner: false,
		        	btnText: false,
		        })
		    })
	}
	onRefresh=()=>{
		this.setState({
			filter: false,
			link:"",
			spinner: false,
			btnText: false,
			alertDaner: true,
			alertSuccess: true,
		})

	}
	onCopy=()=>{
		const {link}=this.state;
	const el = document.createElement('textarea');
		el.value = link;
		document.body.appendChild(el);
		el.select();
		document.execCommand('copy');
		document.body.removeChild(el);


		this.setState({
			alertSuccess: false, 
		})
	}
	onAlertDanger=()=>{
		this.setState({
			alertDaner:true
		})
	}
	onAlertSuccess=()=>{
		this.setState({
			alertSuccess:true
		})
	}
	onSubmit=(e)=>{
		e.preventDefault()
	}
	render() {
		const {link, filter,spinner,btnText, alertDaner,alertSuccess,msg} = this.state;
		let spinnerClass = "spinner d-none"  
		let classFilter = "d-none";
		let btnTextClass = "d-block";
		let alertDanerClass = "alert alert-danger w-50 m-auto d-flex";
		let alertSuccessClass = "alert alert-success w-50 m-auto d-flex";

		if(spinner){
			spinnerClass = "spinner d-block"
		}
		if(filter){
			classFilter = "d-flex flex-nowrap p-1"
		}
		if(btnText){
			btnTextClass = "d-none"
		}
		if(alertDaner){
			alertDanerClass = "d-none"
		}
		if(alertSuccess){
			alertSuccessClass = "d-none"
		}
		return (
			<div className="Forms">
				<form className="d-flex justify-content-center mt-4 pb-4 container" onSubmit={this.onSubmit}>
					<div className="link form-control w-50 input-group text-right d-flex flex-nowrap">
						<input type="text" className="linksInput" value={link} onChange={this.onLink} placeholder="Ссылка, которую Вы хотите сократить" />
						<div className={classFilter}>
							<img src="image/refresh.svg" alt="" className="text-white ml-2 " data-toggle="tooltip" data-placement="top" title="Сбросить" onClick={this.onRefresh}/>
							<img src="image/copy.svg" alt="" className="ml-2" data-toggle="tooltip" data-placement="top" title="Скопировать ссылку" onClick={this.onCopy} />
						</div>
	    			</div>
	    			<button className="btn socratitBtn d-block ml-3" onClick={this.onShort} >
	    				<span className={btnTextClass}>Сократить</span>
	    				<Spinner spinnerClass={spinnerClass}/>
	    			</button>	
				</form>
				<div className=" position-absolute w-100 Massage">
					<div className={alertDanerClass} role="alert" onClick={this.onAlertDanger}>
					 	<img src="image/alert-darning.svg" alt=""/>
					 	<span className="mt-1 ml-1 d-block">{msg}</span>
					</div>
					<div className={alertSuccessClass} role="alert" onClick={this.onAlertSuccess}>
					 	<img src="image/alert-darning.svg" alt=""/>
					 	<span className="mt-1 ml-1 d-block">Вы успешно скапировали ссылку</span>
					</div>
				</div>
			</div>
		)
	} 
}

